// =====================================================================
// Keywords
// =====================================================================

.keywords {
  @extend %l-widget-spacing;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;

  h2 {
    margin-bottom: 8px;
    color: $color-brand;
  }

  .cta {
    margin-right: 8px;
    margin-bottom: 8px;
  }

  .keywords-list {
    @include reset-list;

    margin-top: 10px;
    margin-bottom: 10px;
  }

  .keywords-item {
    display: inline-block;
    border-right: 1px solid $color-whitepale;
    margin-bottom: 8px;
    margin-right: 10px;

    a {
      transition: color 0.3s $easeOutCubic;
      color: $color-grey-light;
      padding-right: 10px;

      &:hover {
        color: $color-grey;
        text-decoration: none;
      }
    }

    &:last-of-type {
      border-right: 0;
    }
  }
}
