// =====================================================================
// Logged in
// =====================================================================

.is-logged-in {
  .user {
    display: flex;
  }

  .cta-holder-subscribe {
    // display: none !important; // stylelint-disable-line declaration-no-important
    #login-cta {
      display: none;
    }

    #subscribe-cta {
      display: none;
    }

    #app-cta {
      display: block;
    }
  }

  .cta-holder-user {
    display: block;
  }

  .topnavbar-login {
    display: none;
  }

  .topnavbar-user {
    display: block;
  }
}
