// =====================================================================
// User
// =====================================================================
.user {
  display: none;

  &.user-dropdown {
    padding-right: 5px;

    @include media-breakpoint-up(lg) {
      align-items: center;
    }

    .no-touchevents & {
      &:hover {
        // background-color: $color-whitepale-light;
        box-shadow: none;

        @include media-breakpoint-up(lg) {
          background-color: transparent;
        }
      }
    }

    .dropdown {
      .no-touchevents & {
        &:hover {
          .dropdown-toggle {
            background-color: transparent;
          }
        }
      }
    }

    .dropdown-toggle {
      text-align: left;

      @include media-breakpoint-up(lg) {
        padding-top: 0;
        padding-bottom: 0;
      }

      &[aria-expanded='true'] {
        .welcome,
        .username,
        .fa-caret-down {
          @include media-breakpoint-down(md) {
            color: $white;
          }
        }
      }

      .welcome {
        color: $color-brand;

        @include media-breakpoint-up(lg) {
          color: $color-grey;
        }
      }

      .username {
        color: $color-brand;

        @include media-breakpoint-up(lg) {
          color: $color-brand;
        }
      }

      .fa-caret-down {
        color: $color-brand;
        display: block;

        @include media-breakpoint-up(lg) {
          display: none;
        }
      }
    }

    .topnav-link {
      padding: 0;

      @include media-breakpoint-up(lg) {
        @include font(12, $letterSpacing: $font-ls-small) ;

        &:hover {
          color: $color-brand;
        }
      }

      .username-not-verify {
        margin: 5px;
        height: 9px;
        width: 9px;
        border-radius: 50%;
        background-color: $color-red-shinmin;
        display: none;
      }
    }

    .dropdown-item {
      padding-top: 10px;
      padding-bottom: 10px;
      color: $color-grey;
      text-align: left;

      @include media-breakpoint-up(lg) {
        text-align: center;
      }
    }

    .divider {
      width: calc(100% - 8px);
      height: 1px;
      margin-left: auto;
      margin-right: auto;
      background-color: $color-grey-lighter;
    }

    .dropdown-menu {
      width: 100%;

      @include media-breakpoint-up(lg) {
        margin-bottom: 0;
        box-shadow: 0 0 4px 0 rgba(58, 58, 58, 0.5);
        // top: 140%;
      }
    }

    .topnav-submenu {
      white-space: normal;
      column-count: auto;
      padding: 0;

      @include media-breakpoint-up(lg) {
        padding-top: 10px;
        padding-bottom: 10px;
      }
    }

    .user-warning {
      color: $color-red-shinmin;
      display: none;

      img {
        padding-right: 1rem;
      }
    }
  }
}
