// =====================================================================
// Section
// =====================================================================

.sec {
  .page-home {
    padding-top: 10px;
    padding-bottom: 10px;

    @include media-breakpoint-up(lg) {
      padding-top: 40px;
      padding-bottom: 40px;
    }

    &:first-of-type {
      padding-top: 0;
    }

    &.sec-videoseries {
      padding-top: 0;
      padding-bottom: 0;

      .group {
        margin-bottom: 0;
      }
    }
  }

  &.sec-filter {
    padding-bottom: 20px;
  }

  &.sec-search-results {
    padding-top: 50px;
  }

  &.sec-freeform {
    padding-top: 0;
    padding-bottom: 0;
    margin-top: 10px;
    margin-bottom: 10px;
    overflow: hidden;
    background-color: $color-whitepale-light;

    .banner {
      background-repeat: no-repeat;
      background-position: center center;
      background-size: cover;
      margin: auto;
      max-width: 100%;
      background-image: url('https://via.placeholder.com/375x230');
      width: 375px;
      height: 230px;

      @include media-breakpoint-up(lg) {
        background-image: url('https://via.placeholder.com/1440x360');
        width: 1440px;
        height: 360px;
      }
    }
  }

