// =====================================================================
// Inline
// =====================================================================

@mixin icon-search {
  font-family: 'Font Awesome 5 Free', sans-serif;
  font-weight: 900;
  -webkit-font-smoothing: antialiased;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
  @include font(40);
  color: $color-white;

  &::before {
    content: '\f00e';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
  }
}

.inline-figure {
  position: relative;

  &.inline-figure-l {
    float: left;
    width: 40%;

    figcaption {
      padding-left: $grid-gutter-width / 2;
      padding-right: $grid-gutter-width / 2;
      background-color: $color-whitepale;
    }
  }

  &.inline-figure-r {
    float: right;
    width: 40%;

    figcaption {
      padding-left: $grid-gutter-width / 2;
      padding-right: $grid-gutter-width / 2;
      background-color: $color-whitepale;
    }
  }
}

.inline-figure-img {

}

.inline-figure-video {

}

.inline-figure-gallery {

}

.figure-media {
  text-align: center;
}

.figure-media-gallery {
  transition: visibility 0.4s $easeOutCubic, opacity 0.4s $easeOutCubic;
  position: relative;
  visibility: hidden;
  opacity: 0;

  .zb-gallery-arrow {
    $arrow-w: 26px;
    $line-w: 3px;
    $line-offset: 12px; // agaration

    position: absolute;
    cursor: pointer;
    z-index: 1;
    top: calc(50% - #{$arrow-w});
    width: $arrow-w;
    height: 40px;

    &:hover {
      .line {
        background-color: $color-white;
      }
    }

    &.zb-gallery-arrow-l {
      left: 15px;

      .line {
        &:first-of-type {
          transform: rotate(-45deg);
        }

        &:last-of-type {
          transform: rotate(45deg);
        }
      }
    }

    &.zb-gallery-arrow-r {
      right: 15px;

      .line {
        &:first-of-type {
          transform: rotate(45deg);
        }

        &:last-of-type {
          transform: rotate(-45deg);
        }
      }
    }

    .line {
      transition: background-color 0.4s $easeOutCubic;
      position: absolute;
      width: $arrow-w;
      height: $line-w;
      border-radius: $line-w / 2;
      background-color: $color-whitepale;

      &:first-of-type {
        top: $line-offset;
      }

      &:last-of-type {
        top: calc(100% - #{$line-offset});
      }
    }
  }

  .slick-slide {
    overflow: hidden;
    outline: none;
  }

  &.slick-dotted {
    &.slick-slider {
      margin-bottom: 0;
    }
  }

  .slick-dots {
    bottom: 20px;

    li {
      margin: 0;

      button {
        padding: 0;

        &::before {
          @include font(10);

          color: $color-white;
          opacity: 1;
        }
      }

      &.slick-active {
        button {
          &::before {
            color: $color-brand;
          }
        }
      }
    }
  }

  &.slick-initialized {
    visibility: visible;
    opacity: 1;
  }
}

.figure-media-gallery-overlay {
  cursor: pointer;
}

.figure-media-gallery-nav {
  @include aspect-ratio(3, 2);

  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;

  .zb-gallery-arrow,
  .slick-dots {
    pointer-events: auto;
  }
}

.figure-media-gallery-slide {
  .article-type-link {
    outline: none;
  }
}

.figure-media-gallery-img {
  @include aspect-ratio(3, 2);
  background-color: $color-whitepale-light;
  position: relative;

  &:hover {
    .tint {
      opacity: 1;
      visibility: visible;
    }
  }

  img {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    max-width: 100%;
    max-height: 100%;
  }

  .tint {
    transition: opacity 0.3s $easeOutCubic, visibility 0.3s $easeOutCubic;
    position: absolute;
    pointer-events: none;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color-black, 0.4);
    opacity: 0;
    visibility: hidden;

    @include icon-search;
  }
}

.inline-blockquote {
  @extend %l-inline-m;

  @include font-notosanssc-medium;

  color: $color-black;
  text-align: center;
  padding: 15px;

  &::before,
  &::after {
    width: 3em;
    content: '';
    height: 5px;
    background-color: $color-brand;
    display: block;
  }

  &::before {
    margin: 0 auto 20px;
  }

  &::after {
    margin: 20px auto 0;
  }
}

.inline-recommend {
  @extend %l-inline-m;

  background-color: $color-whitepale-light;
  border-radius: $br-regular;

  p {
    margin-top: 0;
  }
}

.inline-profile {
  padding: 20px 0;
  border-top: solid 2px $color-brand;
  border-bottom: solid 2px $color-brand;
  text-align: center;

  @include media-breakpoint-up(lg) {
    text-align: left;
  }

  img {
    margin-bottom: 20px;

    @include media-breakpoint-up(lg) {
      margin-bottom: 0;
    }
  }

  p {
    &:first-of-type {
      margin-top: 0;
    }

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  a {
    margin: 20px 0 10px;
  }
}
