// =====================================================================
// Outbrain
// =====================================================================

.OUTBRAIN {
  margin-top: 50px;
  margin-bottom: 50px;

  &[data-widget-id='AR_1'] {
    .AR_1 {
      .ob-widget-section {
        margin-top: 50px;
        margin-bottom: 50px;

        &.ob-first {
          margin-top: 0;
          margin-bottom: 50px;
        }

        &.ob-last {
          margin-top: 50px;
          margin-bottom: 0;
        }
      }

      .ob-widget-header {
        @include font(22, $letterSpacing: $font-ls-small);
        @include font-notosanssc-medium;

        color: $color-brand;
        border-bottom: 2px $color-brand solid;
        margin-bottom: 20px;
      }

      .ob-rec-text {
        @include font-notosanssc-regular;
        @include font(18, $letterSpacing: $font-ls-small);

        color: $color-grey;
      }
    }
  }
}

// example styling - START
.outbrain {
  @extend %l-widget-spacing;

  .heading-border {
    margin-bottom: 15px;
  }
}

.outbrain-item {
  @include media-breakpoint-up(lg) {
  }
}

.outbrain-img {
  @extend %l-outbrain-mb;
}

.outbrain-text {
  padding-left: 10px;
  padding-right: 10px;

  @include media-breakpoint-up(lg) {
    padding-left: 0;
    padding-right: 0;
  }
}

.outbrain-category {
  @extend %l-outbrain-mb;

  display: none;

  @include media-breakpoint-up(lg) {
    display: block;
  }

  p {
    @include font(14, $letterSpacing: $font-ls-regular);

    color: $color-grey-light;
    margin-bottom: 0;
  }
}

.outbrain-title {
  @extend %l-outbrain-mb;

  h2 {
    @include font-notosanssc-regular;
    @include font(18, $letterSpacing: $font-ls-small);

    color: $color-grey;
    margin-bottom: 0;
  }
}

.outbrain-published-date {
  @extend %l-outbrain-mb;

  p {
    @include font(12);

    color: $color-grey-light;
    margin-bottom: 0;
  }
}

.outbrain-source {
  @extend %l-outbrain-mb;

  p {
    @include font(12);

    color: $color-grey-light;
    margin-bottom: 0;
  }
}
// example styling - END
