@mixin font-notosanssc-regular {
  font-family: 'Noto Sans SC', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 400;
}

@mixin font-notosanssc-medium {
  font-family: 'Noto Sans SC', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 500;
}

@mixin font-notosanssc-bold {
  font-family: 'Noto Sans SC', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 700;
}
