// =====================================================================
// Module
// =====================================================================

.heading-border {
  h1,
  h2 {
    @include font-notosanssc-medium;
    @extend %l-aside-item-padding-y;
  }

  a {
    text-decoration: none;
    display: inline-block;
  }

  &.fit {
    padding: 0;

    > [class*='container'] {
      padding: 0;

      > .row {
        margin: 0;

        > .col {
          padding: 0;
        }
      }
    }
  }

  &.heading-border-primary {
    h1,
    h2 {
      color: $color-brand;
      margin-bottom: 0;
      padding-top: 38px;
      padding-bottom: 10px;

      @include media-breakpoint-up (md) {
        padding: 10px 0;
      }
    }

    .divider {
      border-top: solid 1px $color-brand;
    }

    &.solid {
      background-color: $color-brand;
      border-top-left-radius: $br-regular;
      border-top-right-radius: $br-regular;

      h1,
      h2 {
        color: $color-white;
      }
    }
  }

  &.heading-border-secondary {
    h1,
    h2 {
      color: $color-grey-light;
      margin-bottom: 10px;

      @include font(16);

      margin-bottom: 0;
    }

    .divider {
      border-top: solid 2px $color-whitepale;
    }
  }

  &.heading-border-categories {
    h1,
    h2 {
      margin-right: 20px;
    }

    .heading-border-right {
      $w: 70px;

      float: left;
      max-width: 100%;
      white-space: nowrap;

      &::after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: $w;
        height: 100%;
        // background-image: linear-gradient(to right, transparentize($color-white, 1), transparentize($color-white, 0));
      }

      @include media-breakpoint-up(xl) {
        white-space: normal;
        padding-right: 0;
      }
    }

    &.heading-border-categories-listing { // for listing page
      margin-bottom: 20px;

      h1,
      h2 {
        @include font(36, $letterSpacing: $font-ls-small);

        margin-right: 30px;
      }

      .heading-border-sublink {
        @include font(18, $letterSpacing: $font-ls-small);

        margin-right: 20px;
      }

      .divider {
        display: none;
      }

      [class='col'] {
        @include media-breakpoint-down(md) {
          flex: 0 0 100%;

          .heading-border-right {
            max-width: 100%;
          }
        }
      }
    }

    &.header-newsletter-signup {
      h2 {
        margin-bottom: 30px;
        @include font(40);
      }

      p {
        margin-top: 1.66667em;
        margin-bottom: 1.66667em;
        letter-spacing: 0.5px;
      }

      margin-bottom: 0;
      @include media-breakpoint-up(md) {
        margin-bottom: 30px;
      }
    }
  }

  .heading-border-right {
    float: right;

    a {
      @include font(16);

      color: $color-grey-light;
    }

    &::after {
      clear: both;
    }
  }

  .heading-border-right-content {
    $rail-y-h: 4px;

    position: relative;
    padding-top: 6px;
    padding-bottom: 6px;

    .ps__thumb-x {
      height: 0;

      @include media-breakpoint-up(lg) {
        height: $rail-y-h;
      }
    }

    .ps__rail-x {
      // background-color: $color-grey-lighter;
      height: 0;

      @include media-breakpoint-up(lg) {
        height: $rail-y-h;
      }

      &:hover,
      &:focus {
        > .ps__thumb-x {
          // background-color: $color-grey-lighter;
          height: 0;

          @include media-breakpoint-up(lg) {
            height: $rail-y-h;
          }
        }
      }

      &.ps--clicking {
        .ps__thumb-x {
          // background-color: $color-grey-lighter;
          height: 0;

          @include media-breakpoint-up(lg) {
            height: $rail-y-h;
          }
        }
      }
    }
  }

  .heading-border-mainlink {
    h1,
    h2 {
      position: relative;

      &::before {
        transition: opacity 0.4s $easeOutCubic, visibility 0.4s $easeOutCubic, height 0.4s $easeOutCubic;
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        background-color: $color-brand;
        height: 0;
        opacity: 0;
        visibility: hidden;
      }
    }

    &:hover {
      h1,
      h2 {
        &::before {
          height: 2px;
          opacity: 1;
          visibility: visible;
        }
      }
    }
  }

  .heading-border-sublink {
    transition: color 0.4s $easeOutExpo;
    display: inline-block;
    margin-right: 20px;

    &:hover {
      color: $color-grey;
    }
  }

  .divider {
  }

  .heading-border-padding-top {
    padding-top: 30px;
    @include media-breakpoint-up(md) {
      padding-top: 20px;
    }

    .heading-border-right {
      float: none;
      white-space: normal;

      .heading-border-right-content {
        text-align: center;

        p {
          font-weight: 500;
        }

        a {
          color: $color-brand;
          @include font (16);
          font-weight: 500;
          margin: 0 15px;

          .link-underline {
            color: $color-brand;
          }

          .icon-arrow-down {
            width: 13px;
            height: auto;
          }
        }
      }

      .epaper-border-right-content {
        text-align: center;

        p {
          font-weight: 500;
        }

        a {
          color: $color-brand;
          @include font (16);
          font-weight: 500;
          margin: 0;

          .link-underline {
            color: $color-brand;
            @include font (16);

            @include media-breakpoint-up (md) {
              @include font (18);
            }
          }

          .icon-arrow-down {
            width: 13px;
            height: auto;
          }

          @include media-breakpoint-up (md) {
            margin: 0 15px;
          }
        }

        .link-container {
          display: flex;
          justify-content: space-between;

          @include media-breakpoint-up (md) {
            justify-content: center;
          }
        }
      }
    }
  }
}
