// =====================================================================
// Topnavbar (Mobile)
// =====================================================================

.topnavbar-mobile {
  position: relative;
  z-index: 3;
  background-color: $color-white;

  .topnav-link {
    @extend %l-topnav-link-padding-y;
  }

  .hamburger {
    @include hamburger($ham-w, $ham-h, 3px, $color-brand);

    > div {
      border-radius: 1.5px;
    }
  }

  .topnavbar-header {
    transition: box-shadow 0.7s $easeOutCubic;
    position: relative;
    height: $topnavbar-header-h;

    .is-scrolled-down & {
      box-shadow: 0 1px 7px 0 rgba($color-whitepale, 0.5);
    }

    > .container {
      height: 100%;

      > .row {
        height: 100%;
      }
    }

    .logotype {
      transition: transform 0.4s $easeOutCubic, opacity 0.4s $easeOutCubic, visibility 0.4s $easeOutCubic;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);

      .svg-logotype-type {
        transition: visibility 0.4s 0.3s $easeOutCubic, opacity 0.4s 0.3s $easeOutCubic;
        visibility: visible;
        opacity: 1;
      }
    }

    .mobile-text-links {
      transition: visibility 0.4s $easeOutCubic, opacity 0.4s $easeOutCubic;
      text-align: center;
      position: absolute;
      visibility: hidden;
      opacity: 0;
      height: 40px;
      margin-top: -20px;
      top: 50%;
      left: 50%;
      transform: translateX(-50%);
      width: 220px;
    }

    .mobile-text-links-list {
      position: relative;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;

      a {
        @include font(13);
        color: $color-brand;
      }
    }

    .mobile-text-links-item {
      transition: visibility 0.5s $easeInOutCubic, opacity 0.5s $easeInOutCubic;
      width: 100%;
      flex: 0 1 auto;
      position: absolute;
      opacity: 0;
      visibility: hidden;

      &.is-active {
        position: relative;
        opacity: 1;
        visibility: visible;
      }

      a {
        // @include truncate;
        width: 100%;

        @include font(11);
        @include media-breakpoint-up(sm) {
          @include font(13);
        }
      }
    }

    .cta-holder-subscribe {
      display: flex;
      align-items: center;

      a {
        min-width: 0;
        letter-spacing: normal;
        @include font(13);
        @include media-breakpoint-up(sm) {
          @include font(13);
        }
      }
    }
  }

  .topnavbar-body {
    position: relative;
    z-index: 1;
    background-color: $color-brand;
    overflow: auto;
    -webkit-overflow-scrolling: touch;

    // backface-visibility: hidden;
    // transform-style: preserve-3d;
    // will-change: transform;
    // transform: translateZ(0);

    @include media-breakpoint-up(lg) {
      overflow: visible;
      max-height: none;
    }
  }

  .topnavbar-search {
    padding: 15px 0;
  }

  .topnavbar-fontsizing,
  .topnavbar-featured {
    padding: 20px 0;
  }

  .topnavbar-news-list,
  .topnavbar-login {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  .topnavbar-news-list {
    li {
      &:hover {
        background: $color-whitepale-light;

        .fa-caret-down {
          color: $color-brand;
        }

        .topnav-link {
          color: $color-brand;
        }
      }
    }
  }

  .topnav-link-listing {
    .no-touchevents & {
      &:hover {
        color: $color-white; // to overwrite mobile behaviour
      }
    }
  }

  .user {
    // background: $white;
    &.user-dropdown {
      padding-right: 0;

      .dropdown-toggle {
        text-align: center;
        background: $white;

        .fa-caret-down {
          float: none;
          display: inline-block;
        }
      }

      .topnav-link {
        padding-top: 20px;
        padding-bottom: 20px;
      }

      &:hover,
      :active {
        .welcome,
        .username,
        .fa-caret-down {
          color: $color-white;
        }
      }
    }
  }

  .dropdown {
    cursor: pointer;

    .no-touchevents & {
      &:hover {
        .topnav-link-listing {
          // background-color: $color-whitepale-light;
          color: $color-brand;
        }

        .dropdown-toggle {
          // box-shadow: none;
        }

        .fa-caret-down {
          // color: $color-brand;
        }
      }
    }

    &.show {
      .topnav-link-listing {
        background-color: $color-whitepale-light;
        color: $color-brand;
      }
    }
  }

  .dropdown-toggle {
    width: 100%;

    &[aria-expanded='true'] {
      // background-color: $color-brand !important;
      // color: $color-brand;
      // .welcome {
      //   color: $white;
      // }

      .fa-caret-down {
        transform: rotate3d(1, 0, 0, 180deg);
        color: $color-brand;
      }
    }

    &::after {
      display: none;
    }

    .fa-caret-down {
      transition: transform 0.5s $easeOutCubic;
      float: right;
      @include font(18);
      margin-top: 10px; // agaration
      margin-right: 15px;
      color: $color-white;
    }
  }

  .dropdown-menu {
    // prevent -webkit-overflow-scrolling: touch from flickering
    top: auto !important; // stylelint-disable-line declaration-no-important
    left: auto !important; // stylelint-disable-line declaration-no-important
    overflow: hidden;
  }

  .divider {
    width: calc(100% - 20px);
    height: 1px;
    margin: 0 auto;
    background-color: transparentize($color-grey-light, 0.5);
  }

  .topnavbar-featured {
    text-align: center;

    .topnav-link {
      @include font(14, $isEm: true);

      padding: 10px 0;
    }
  }

  .topnavbar-news {
    width: 100%;

    .topnav-link {
      @include font(18, $isEm: true);
    }

    .dropdown-menu {
      @include font(18, $isEm: true); // reset bootstrap definition
    }

    .topnav-submenu {
      @include font(10, $isEm: true); // unset compounding

      .topnav-link {
        @include font(9, $isEm: true); // unset compounding, agar to 16px
      }
    }
  }

  .topnavbar-login {
    // margin-bottom: $topnavbar-mobile-allowance-mb;
    text-align: center;
    padding: 20px 0;

    .topnav-link {
      @include font(16, $letterSpacing: 0, $isEm: true);
      height: 36px;
      border-radius: 30px;
      min-width: 100px;
    }

    .topnav-link-cancel-padding {
      padding: 2px 16px;
    }
  }

  .topnavbar-user {
    display: none;
    // background: $color-whitepale;
    // margin-bottom: $topnavbar-mobile-allowance-mb;

    .dropdown-menu {
      @include font(18, $isEm: true); // reset bootstrap definition
    }

    .topnav-link {
      @include font(12, $letterSpacing: 0, $isEm: true);
    }

    .topnav-submenu {
      @include font(10, $isEm: true); // unset compounding

      .topnav-link {
        @include font(9, $isEm: true); // unset compounding, agar to 16px
      }
    }
  }
}

.topnavbar-search {
  .searchbar {
    position: relative;
    width: 100%;
    height: 32px;
  }

  .svg-icon-search {
    position: absolute;
    top: 5px;
    left: 12px;
    padding: 0;
  }

  input {
    &[type='search'] {
      width: 100%;
      height: 100%;
      border-radius: 16px;
      background-color: rgba(76, 0, 0, 0.5);
      border: 0;
      padding-left: 50px;
      color: $color-white;

      @include font(16, 32px);

      &::placeholder {
        @include font(16, $letterSpacing: 0.5px);

        color: transparentize($color-grey-light, 0.5px);
      }
    }
  }
}

.topnavbar-fontsizing {
  text-align: center;

  p {
    color: $color-white;
  }

  .topnavbar-fontsizing-title {
    display: inline-block;
    float: left;
    padding-top: 3px;

    p {
      margin-bottom: 0;
      @include font(16);
    }
  }

  .fontsizing-range-list {
    display: inline-block;
    float: left;

    li {
      display: inline-block;
      margin: 0 8px;
    }

    .cta-bordered {
      border-radius: 50%;
      width: 30px;
      @include font (13);
      // padding-left: 12px;
      // padding-right: 9px;
      padding: 0 7px;
    }
  }

  .switch-button {
    float: right;
    display: inline-block;
    margin-right: 0;
  }
}
