// =====================================================================
// Topnavbar (Primary)
// =====================================================================

.topnavbar-primary {
  background-color: $color-white;
  z-index: 3;

  .topnav-link {
    @include font(14, $cta-h);
    @include font-notosanssc-regular;

    color: $color-grey;

    &:hover {
      color: $color-brand;
    }
  }

  .topnavbar-featured {
    padding: 15px 0;
  }

  .divider-line {
    height: 30px;
    width: 1px;
    background-color: $color-whitepale;
  }

  .cta-holder-subscribe {
    display: flex;
    float: right;
    overflow: visible;
    margin-left: 20px;

    .cta-grey {
      @include font (12);
      letter-spacing: 1px;
      padding: 0 8px;
      // @include font (15);
    }

    .cta-gradient-red {
      @include font (14);
      letter-spacing: 1px;
      padding: 0 16px;
      // @include font (15);
    }
  }

  .cta-holder-user {
    display: none;
    overflow: visible;

    .no-touchevents & {
      &:hover {
        background-color: transparent;
        box-shadow: none;
      }
    }
  }
}
