// =====================================================================
// Overlay
// =====================================================================

.overlay {
  transition: visibility 0.5s $easeOutCubic, opacity 0.5s $easeOutCubic;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  visibility: hidden;
  opacity: 0;

  &.is-active {
    z-index: 999;
    visibility: visible;
    opacity: 1;

    .overlay-wrap {
      transform: scale(1);
    }
  }
}

.overlay-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  &.overlay-bg-grey {
    background-color: rgba($color-black, 0.6);
  }
}

.overlay-wrap {
  transition: transform 0.5s $easeOutCubic;
  position: relative;
  transform: scale(0.9);
  margin-left: auto;
  margin-right: auto;
  background-color: $color-white;
  box-shadow: 0 0 9px 0 rgba(49, 49, 49, 0.5);
  max-width: calc(100vw - #{$grid-gutter-width * 2});

  @include media-breakpoint-up(lg) {
    max-width: 880px;
  }

  .cancel {
    transition: background-color 0.4s $easeOutCubic;
    $w: 36px;

    position: absolute;
    z-index: 2;
    top: -($w / 2);
    right: -($w / 2);
    width: $w;
    height: $w;
    cursor: pointer;
    border-radius: 50%;
    border: solid 2px $color-white;
    background-color: transparentize($color-grey, 0.5);

    &:hover {
      background-color: transparentize($color-grey, 0.25);

      &::before {
        transform: rotate(45deg) scale(0.9);
      }

      &::after {
        transform: rotate(-45deg) scale(0.9);
      }
    }

    &::before,
    &::after {
      transition: transform 0.4s $easeOutCubic;
      content: '';
      width: 2px;
      height: 20px;
      background-color: $color-white;
      position: absolute;
      top: calc(50% - 10px);
      left: calc(50% - 1px);
    }

    &::before {
      transform: rotate(45deg) scale(1);
    }

    &::after {
      transform: rotate(-45deg) scale(1);
    }
  }
}

.overlay-content {
  -webkit-overflow-scrolling: touch;
  overflow: auto;
  max-height: calc(100vh - 60px);

  @include media-breakpoint-up(lg) {
    max-height: 100%;
  }

  &.overlay-content-search {

  }

  .overlay-content-search-query {

  }

  .overlay-content-search-keywords {

  }
}

.overlay-microtransaction {
  .overlay-content {
    padding-top: 30px;

    @include media-breakpoint-up(sm) {
      padding-top: 60px;
    }
  }

  .label {
    &.label-premium {
      vertical-align: top;

      @include label-premium-small;

      margin-top: 3px;

      @include media-breakpoint-up(sm) {
        @include label-premium-regular;

        margin-top: 5px;
      }
    }
  }

  .microtransaction-header {
    text-align: center;

    p {
      @include font(15, $letterSpacing: $font-ls-small);

      margin-bottom: 4px;

      @include media-breakpoint-up(sm) {
        @include font(16, $letterSpacing: $font-ls-small);
      }
    }
  }

  .microtransaction-body {
    margin-top: 40px;
    margin-bottom: 40px;
    text-align: left;

    @include media-breakpoint-up(sm) {
      margin-top: 60px;
      margin-bottom: 60px;
      text-align: center;
    }

    @include media-breakpoint-up(lg) {
      text-align: left;
    }

    > .row {
      > [class*='col-'] {
        @include media-breakpoint-up(lg) {
          border-right: 1px solid $color-whitepale;

          &:last-of-type {
            border-right: 0;
          }
        }
      }
    }

    .cta {
      margin-bottom: 8px;

      @include media-breakpoint-up(lg) {
        margin-bottom: 20px;
      }
    }
  }

  .microtransaction-option {
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 30px;

    @include media-breakpoint-up(lg) {
      margin-bottom: 0;
    }

    p {
      color: $color-grey-light;

      @include font(14);
    }
  }

  .svg-icon-play-solid {
    max-width: 20px;
  }

  .icon-unlock {
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(calc(50% - 10px), calc(-100% + 15px));
    max-width: 40px;

    @include media-breakpoint-up(lg) {
      max-width: $icon-unlock-maxw;
    }
  }

  .avt {
    margin-bottom: 0;
  }
}

.overlay-search {
  .keywords {
    padding-top: 0;
    padding-bottom: 30px;
  }
}

.overlay-onboarding {
  .flex-center-v-content {
    @include media-breakpoint-down(sm) {
      height: 100%; // fix mobile url bar covering top and bottom parts
    }
  }

  .overlay-wrap {
    $curve-top: 150px;

    @include media-breakpoint-down(sm) {
      max-width: none;
      height: 100%;

      &::before,
      &::after {
        content: '';
        position: absolute;
      }

      &::before {
        top: $curve-top;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(to bottom, $color-blue-light, $color-blue);
      }

      &::after {
        top: $curve-top / 2;
        left: 50%;
        transform: translateX(-50%);
        background-color: $color-white;
        border-radius: 50%;
        width: 150vw;
        height: $curve-top;
      }
    }
  }

  .cancel {
    @include media-breakpoint-down(sm) {
      top: 10px;
      right: 10px;
    }
  }

  .onboarding-link {
    transition: color 0.4s $easeOutCubic;

    @include font(14, $letterSpacing: $font-ls-small);

    text-decoration: none;
    color: $color-grey-lighter;
    margin-top: 20px;
    margin-bottom: 20px;
    display: none;

    &:hover {
      color: $color-brand;
    }

    @include media-breakpoint-up(md) {
      display: inline-block;
    }
  }

  .overlay-content-onboarding {
    position: relative;
    z-index: 1;
    background-position: 100% center;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: none;
    padding: ($grid-gutter-width / 2) 0;

    @include media-breakpoint-up(md) {
      background-image: url('./../images/onboarding/onboarding-desktop-01.png');
      padding: 30px 10px;
    }
  }

  .svg-logotype {
    max-width: 136px;
  }

  .onboarding-wrap {
    position: relative;
  }

  .onboarding-text {
    transition: opacity 0.5s $easeOutCubic, visibility 0.5s $easeOutCubic;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    visibility: hidden;
    opacity: 0;
    text-align: center;
    padding: 20px;

    @include media-breakpoint-up(md) {
      text-align: left;
      padding: 120px 20px;

      &:first-of-type {
        position: relative;
        visibility: visible;
        opacity: 1;
      }
    }

    &.is-active {
      @include media-breakpoint-down(sm) {
        position: relative;
        visibility: visible;
        opacity: 1;
      }
    }
  }

  .onboarding-header {
    margin-bottom: 10px;

    @include media-breakpoint-up(md) {
      margin-bottom: 20px;
    }

    p {
      margin-bottom: 0;
      text-align: center;
    }
  }

  .onboarding-body {

  }

  p {
    color: $color-grey-light;

    @include media-breakpoint-down(sm) {
      @include font(15);
    }
  }

  // h2 {
  //   text-align: center;
  //   @include font(18);
  //   margin-bottom: 0;
  // }

  h1 {
    @include font(38, $letterSpacing: $font-ls-medium);
    @include font-notosanssc-medium;

    color: $color-brand;
  }

  .onboarding-gallery {
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    margin-bottom: 0;
    display: block;

    @include media-breakpoint-up(md) {
      display: none;
    }

    .slick-dots {
      bottom: 10px;

      li {
        margin: 0;

        button {
          &::before {
            opacity: 1;

            @include font(10);

            color: $color-white;
          }
        }

        &.slick-active {
          button {
            &::before {
              color: $color-brand;
            }
          }
        }
      }
    }
  }

  .onboarding-gallery-slide {
    padding-top: 30px;
    padding-bottom: 30px;

    img {
      margin-left: auto;
      margin-right: auto;
      max-width: 240px;

      @include media-breakpoint-up(sm) {
        max-width: 400px;
      }
    }
  }
}

.overlay-global-page {
  visibility: hidden;
  opacity: 1;
  z-index: 4;

  .overlay-wrap {
    max-width: 100%;
    transform: scale(1);
    height: 100vh;

    .cancel {
      transition: background-color 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
      position: absolute;
      z-index: 2;
      top: 30px;
      right: 30px;
      width: 16px;
      height: 16px;
      cursor: pointer;
      border-radius: none;
      border: none;
      background: none;

      &::after {
        background-color: $color-grey-light;
      }

      &::before {
        background-color: $color-grey-light;
      }
    }

    @include media-breakpoint-up (md) {
      transform: scale(0.9);
      max-width: 1260px;
      height: auto;
    }

    .overlay-content-global {
      overflow-y: auto;
      position: relative;
      width: 100%;
      display: none;
      // min-height: 100vh;
      background-color: white;
      background-image: url('/dist/images/common/Oval.svg');
      background-repeat: no-repeat;
      background-size: 100%;
      // max-height: max-content;
      max-height: 100vh;

      @include media-breakpoint-up (md) {

        // min-height: 855px;
        max-height: 100vh;
      }

      @media (min-height: 870px) {
        overflow-y: hidden;
      }

      .overlay-content-container {
        .overlay-header-container {
          text-align: center;
          padding: 40px 50px 0;

          @include media-breakpoint-up(sm) {
            margin: 0 auto;
            padding: 40px 15px 0;
          }

          h2 {
            @include font(24);
          }

          p {
            @include font (18);
          }
        }

        .overlay-body-container {
          // margin: 16px 0 0;
          width: 100%;
          overflow: scroll hidden;
          display: flex;
          flex-direction: row;
          flex-wrap: nowrap;
          justify-content: start;
          padding: 0 40px 30px;
          // padding-bottom: 30px;
          border-bottom: 1px solid $color-whitepale;

          @include media-breakpoint-up (md) {
            // padding: 0 2% 30px;
          }

          @include media-breakpoint-up (xl) {
            // margin: 24px 24px 0;
            // padding: 0 2% 30px;
            overflow: auto hidden;
          }
          // scroll-snap-align: center;
          // &::-webkit-scrollbar {
          //   visibility: visible;
          //   @include media-breakpoint-up (lg) {
          //     visibility: hidden;
          //   }
          // }

          .single-global-container {
            scroll-snap-align: center;
            -webkit-overflow-scrolling: touch;
            // display: inline-block;
            width: 280px;
            margin: 0 10px;

            @include media-breakpoint-up (md) {
              width: 342px;
              margin: 0 25px;
            }

            .pointer-container {
              padding-bottom: 5px;
              text-align: center;
              visibility: hidden;

              .caption {
                color: $color-brand;
                margin-bottom: 0;
                @include font(14);
              }

              img {
                width: 11px;
              }

              &.active {
                visibility: visible;
              }
            }

            .single-container {
              position: relative;
              // min-height: unset;
              height: 544px;
              box-shadow: 0 0 10px rgba(103, 103, 103, 0.25);
              background-color: $color-white;

              @include media-breakpoint-up (md) {
                height: 460px;
              }

              &.highlight {
                border: 1px solid $color-brand;
              }

              .header-container {
                padding: 26px 40px;
                background-color: $color-brand;
                height: 54%;
                min-height: unset;
                position: relative;

                @include media-breakpoint-up (md) {
                  padding: 26px;
                  height: 195px;
                  min-height: 195px;
                }

                .img-container {
                  // position: absolute;
                  top: 26px;
                  width: 200px;
                  min-height: 261px;

                  @include media-breakpoint-up (md) {
                    width: 290px;
                  }

                  img {
                    width: 100%;
                  }

                  .desktop {
                    padding: 15px;
                    background: $color-white;
                    box-shadow: 0 0 10px rgba(103, 103, 103, 0.25);
                    display: none;
                    @include media-breakpoint-up (md) {
                      display: block;
                    }
                  }

                  .mobile {
                    padding: 15px;
                    background: $color-white;
                    box-shadow: 0 0 10px rgba(103, 103, 103, 0.25);
                    display: block;
                    @include media-breakpoint-up (md) {
                      display: none;
                    }
                  }
                }
              }

              .body-container {
                position: absolute;
                top: 67%;
                width: 100%;
                // padding-top: 11vw;
                // padding-bottom: 8%;
                min-height: auto;

                @include media-breakpoint-up (md) {
                  padding-bottom: 1vw;
                  // bottom: 0;
                  top: 61%;
                  height: 266px;
                }

                ul {
                  margin: 0 auto;
                  padding: 0 30px 20px 55px;
                  list-style-type: none;
                  list-style-image: url('.././images/icon/red-tick.svg');

                  @include media-breakpoint-up (md) {
                    padding: 0 45px 20px 65px;
                  }

                  li {
                    @include font (14);
                    line-height: 150%;
                  }
                }

                h3 {
                  text-align: center;
                  font-weight: 500;
                }
              }

              .cta-container {
                width: 100%;
                text-align: center;
                position: absolute;
                bottom: 28px;

                @include media-breakpoint-up (md) {
                  bottom: 30px;
                }
              }
            }
          }
        }

        .desktop-footer-container {
          margin: 0 auto;
          padding: 30px 0;
          text-align: center;
          display: none;

          @include media-breakpoint-up (md) {
            display: block;
          }

          img {
            width: 648px;
          }
        }

        .mobile-footer-container {
          display: block;
          margin: 0 30px;
          padding-top: 20px;
          padding-bottom: 20px;
          text-align: center;

          @include media-breakpoint-up (md) {
            display: none;
          }

          img {
            width: 100%;
          }
        }
      }
    }
  }
}
