// =====================================================================
// Content
// =====================================================================

.content {

}

.content-primary {
  .page-article & {
    max-width: 100%;

    @include media-breakpoint-up(lg) {
      max-width: 500px;
    }

    @include media-breakpoint-up(xl) {
      max-width: 100%;
    }

    @include media-breakpoint-up(xxl) {
      max-width: 860px;
    }

    .label-topic {
      margin-bottom: 20px;
    }
  }

  // drupal output html outside of .article-content
  figure {
    img {
      max-width: 100%;
    }

    figcaption {
      text-align: center;
      color: $color-grey-light;
      padding: l-em($font-base-size, 20) 0;
      margin-top: l-em($font-base-size, 0);
      margin-bottom: l-em($font-base-size, 0);

      @include font(13, 1.4, $isEm: true);

      @include media-breakpoint-up(lg) {
        @include font(14, 1.5, $isEm: true);
      }
    }

    &.image-placeholder { // legacy css class
      text-align: center;
    }
  }

  // all fontsizing related and spacing em styles
  .article-content-rawhtml {
    @include font($font-base-size, 1.94, 0.5px, $isEm: true);

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      @include font-notosanssc-medium;
    }

    h1 {
      @include font(11, 1.94, $isEm: true);
    }

    h2,
    h3,
    h4,
    h5,
    h6 {
      @include font(10, 1.94, $isEm: true);
    }

    p {
      font-size: em(10); // unset compounding
      margin-top: l-em($font-base-size, 30);
      margin-bottom: l-em($font-base-size, 30);

      a {
        @include font(inherit, $isEm: true);
      }
    }

    figure {
      margin-bottom: l-em($font-base-size, 40);

      &.inline-figure-l {
        margin-right: l-em($font-base-size, 20);
        margin-bottom: l-em($font-base-size, 20);
      }

      &.inline-figure-r {
        margin-left: l-em($font-base-size, 20);
        margin-bottom: l-em($font-base-size, 20);
      }

      figcaption {
        @include font(7, 1.4, $isEm: true); // unset compounding, agar to 13

        @include media-breakpoint-up(lg) {
          @include font(8, 1.5, $isEm: true); // unset compounding, agar to 14
        }
      }
    }

    .inline-blockquote {
      @include font(11, 1.94, $isEm: true); // unset compounding, agar to 20
    }

    .inline-profile {
      a {
        @include font(10, $isEm: true); // unset compounding, agar to 18
      }
    }

    .inline-recommend {
      p {
        margin-top: 0;
      }
    }

    .avt {
      margin-top: l-em($font-base-size, 40);
      margin-bottom: l-em($font-base-size, 40);
    }
  }
}
