// =====================================================================
// Article (detail)
// =====================================================================

.article {

}

.article-list {
  @include reset-list;

  > li {
    .article-type {
      border-bottom: 1px solid $color-whitepale-light;
    }

    &:last-of-type {
      .article-type {
        border-bottom: 0;
      }
    }

    @include media-breakpoint-up(lg) {
      .article-type {
        border-bottom: 0;
      }
    }
  }

  &.article-list-bordered {
    > li {
      .article-type {
        border-bottom: 1px solid $color-whitepale-light;
      }

      &:last-of-type {
        .article-type {
          border-bottom: 0;
        }
      }
    }
  }
}

.article-title {
  margin-top: 10px;
  margin-bottom: 10px;

  h1 {
    @include font-notosanssc-regular;
    @include font(36, $letterSpacing: $font-ls-regular);

    margin-bottom: 0;
  }
}

.article-meta {
  padding: 15px 0;
  margin: 15px 0;
  border-bottom: 1px solid $color-whitepale-light;
}

.article-byline {
  h4 {
    @include font(14);
    @include font-notosanssc-regular;

    margin-bottom: 6px;

    a {
      @include font(14);

      margin-right: 4px;

      &:last-of-type {
        margin-right: 0;
      }
    }

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  .title-byline {
    color: $color-grey-light;
  }
}

.article-fontsizing {
  text-align: center;
  margin-top: 30px;
  margin-bottom: 30px;

  .fontsizing-title {
    display: inline-block;
    margin-right: l-em($font-base-size, 5);

    p {
      @include font(16);

      color: $color-brand;
      margin-bottom: 0;
    }
  }

  .fontsizing-range-list {
    @include reset-list;

    display: inline-block;

    li {
      @include font(16);

      display: inline-block;
      cursor: pointer;
      margin-left: l-em($font-base-size, 5);
      margin-right: l-em($font-base-size, 5);

      &:last-of-type {
        margin-right: 0;
      }

      &:hover {
        color: $color-brand;
      }

      &.is-active {
        color: $color-brand;

        @include underline(0.5, -2px);
      }
    }
  }
}

.article-content {
  .is-premium & {
    position: relative;

    // &::after {
    //   content: '';
    //   position: absolute;
    //   bottom: 0;
    //   left: 0;
